import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { HttpProvider } from '../../providers/http/http';
import { UtilsProvider } from '../../providers/utils/utils';
import { Storage } from '@ionic/storage';
import { CommandsProvider } from '../../providers/commands/commands';

/**
 * Generated class for the BillsToPayPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-bills-to-pay',
  templateUrl: 'bills-to-pay.html',
})
export class BillsToPayPage {

  public cuentasPropias: string;
  public message: string; 
  public lista1 : string;
  public lista2 : string;

  public envioFacturaNueva: number;

  public requestSecurity: string[]; //Variable del OTP
  //obtencion del valided de security otp para no volver a pedir el OTP
  public valide: boolean;
  public procesoOTP: number=0;

  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, public http: HttpProvider, public utils: UtilsProvider,
              public loading: LoadingController, public command: CommandsProvider) {
  
                this.valide = this.navParams.get('valided');

                if(""!=this.navParams.get('valided') && this.navParams.get('valided')!=undefined){
                  this.valide = this.navParams.get('valided');
                }
                this.storage.get('procesoOTP').then((val) => {
                  if (val != null) { 
                    this.procesoOTP = val;
                  }     
                });
                this.procesoOTP = this.utils.getProcesoOTP();

                // para cargar mas rapido las cuentas que vienen del OTP 
                this.cargarloadPayBills();

                //Obtención de las cuentas a debitar
                this.storage.get('cuentasPropias').then((valor) => {
                  this.cuentasPropias = valor;
                });

    //obtencion de la bandera que se envia desde ruta pago para cuando se adicione
    //una factura cargue de nuevo y haga la peticion al servidor y se vea la factura
    this.envioFacturaNueva = ( this.navParams.get("envioFacturaNueva") ) == null ? "null" : this.navParams.get("envioFacturaNueva"); 
    //alert("valor nueva factura-> "+this.envioFacturaNueva );
  }


  ionViewCanEnter(){  

    // para cargar mas rapido las cuentas que vienen del OTP 
    this.cargarloadPayBills();
    
    var wait:boolean=true;
    var canView:boolean=true;
    this.procesoOTP = this.procesoOTP = this.utils.getProcesoOTP();
    this.requestSecurity = this.utils.getCommands(this.command.E2LSMR6081);
     
        if (this.requestSecurity[1] == this.utils.opt_validator && this.procesoOTP!=1 && this.valide!=true) {
            this.loadPayBills(); 
            this.solicitudOTP();
            canView = false;        
        }
        else{    
            this.loadPayBills();        
            canView = true;       
        }  
    return canView;
  }

  private solicitudOTP(){
    
    //Valor para solicitar el OTP
    this.requestSecurity = this.utils.getCommands(this.command.E2LSMR6081);
    this.valide = this.navParams.get('valided');

    //Obtencion de la variable de sesion para no volver a pedir el OTP
    this.storage.get('procesoOTP').then((val) => {
      if (val != null) {
        this.procesoOTP = val;
      }
      this.navToOTP();
    });

  }

  private navToOTP(){    

    if (this.requestSecurity[1] == '*' && this.procesoOTP!=1 && this.valide!=true) {
      this.navCtrl.push("SecurityOtpPage",{irPantalla: "BillsToPayPage",  cmdAux: 'E2LSMR6081' }); 
    }

  } 

  //metodo para cargar las listas enviadas desde security OTP la cual antes se cargaba en loadPayBills
  private cargarloadPayBills(){

    this.storage.get('cuentasObtenidasOTP_BillsToPayPage_1').then((val) =>    {  
        this.lista1 = val;
        this.storage.set("payList1",  this.lista1);
    });

    this.storage.get('cuentasObtenidasOTP_BillsToPayPage_2').then((val) =>    {  
      this.lista2 = val;
      this.storage.set("payList2",  this.lista2);
      //asignacion de cuentas propias
      this.storage.set('misCuentas', this.lista2);
    });
        
    //variable para redirigir a producto a pagar pay bill
    this.storage.set('bandera', "banderaPayBill"); 

  }

  ionViewDidLoad() { 
    this.storage.get('payList1').then((val) =>    { if(val != null ){ this.lista1 = val; } });
    this.storage.get('payList2').then((val) =>    { if(val != null ){ this.lista2 = val; } });
  }

  ionViewDidEnter(){    

    if(this.envioFacturaNueva == 1){
      this.presentLoadingDefault();
      this.loadPayBills(); 
    }

  }


  private loadPayBills() {   
     
    this.cargarloadPayBills();
    //var command = 'E2LSMR6081'; 
    var command :string[] =  this.utils.getCommands(this.command.E2LSMR6081); 
    
    //Entra la primera vez y necesita OTP realizar el CALL
    if(this.requestSecurity[1] == this.utils.opt_validator && this.procesoOTP!=1 && this.valide!=true)
    {
      if (this.utils.getCommands != null && this.utils.getCommands != undefined && this.utils.getCommands.length > 0) 
      { 
        var param = command[0] + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+'&E2EW=%BA' + this.utils.separator + '%BA' + this.utils.separator + '0%BA' + this.utils.separator + '%BA' + this.utils.separator;
        this.http.callServer(param).then(res => {      
          this.message = res.message;
      
            if (this.message != '' && typeof this.message!=="undefined") {
              this.utils.alertServidor(this.message);
            } else {
              this.cargarloadPayBills(); 
            }
        });
      }
    }//Entra proveniente del securiti-otp y ya los datos estan en sesion
    else if(this.requestSecurity[1] == this.utils.opt_validator && this.procesoOTP==1)
    {
      this.cargarloadPayBills();
    }
    //Entra cuando no se necesita hacer llamado al OTP y hace proceso completo
    else if(this.requestSecurity[1] != this.utils.opt_validator)
    {   
      if (this.utils.getCommands != null && this.utils.getCommands != undefined && this.utils.getCommands.length > 0) 
        {
          var param = command[0] + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+'&E2EW=%BA' + this.utils.separator + '%BA' + this.utils.separator + '0%BA' + this.utils.separator + '%BA' + this.utils.separator;

          this.http.callServer(param).then(res => {      
          this.message = res.message;

            if (this.message != '' && typeof this.message!=="undefined") {
              this.utils.alertServidor(this.message);
            } else {
              this.lista1 = res.lista1;
              this.lista2 = res.lista2;
              this.storage.set("payList1",  this.lista1);
              this.storage.set("payList2",  this.lista2);
              //asignacion de cuentas propias
              this.storage.set('misCuentas', this.lista2);
              
              //variable para redirigir a producto a pagar pay bill
              this.storage.set('bandera', "banderaPayBill"); 
            }
          },
          error => {
            console.log(error);
          });
        }
    }

    
  }

  //Metodo de envio a la pagina de productos a pagar de pay bills
  productToPayBill(i, j, cuentaAsociadaFactura){
    //Variable sesion de la cuenta seleccionada
    this.storage.set("cuentaPayBill", i);
    this.storage.set("infoCuenta", this.lista1[j]);
    
    //numero de cuenta asociada a la factura
    this.storage.set("cuentaAsocFactSelec", cuentaAsociadaFactura);

    //Posición del elemento seleccionado de la lista de elementos de paybills
    this.storage.set('posAccount', j);

    //Se guarda la lista 2 donde guarda todas las cuentas propias que se tiene
    //this.storage.set('bandera', "banderaPayBill"); 
                                              //se envia bandera para que guarde en sesion la cuenta que esta 
                                              //asociada a la factura y si selecciona otra cuenta cambie el valor
    this.navCtrl.push("ProductToPayBillPage", { flagCuentaAsociada: 1 }); 
    
  }

  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }

  //Metodo Loanding
  presentLoadingDefault() {
    let loading = this.loading.create({
      content: 'Please wait...'
    });
  
    loading.present();
  
    setTimeout(() => {
      loading.dismiss();
    }, 1500);
  }
  
}



